/***
  vds Character Counter
  BEM Naming Convention http://getbem.com/naming/

  @author nrusso
*/
$color-character-counter-message: theme(input-label);
$color-character-counter-error: theme(notification-error);

.vds-character-counter {
  color: $color-character-counter-message;
  white-space: nowrap;

  &--near-limit {
    font-weight: bold;
  }

  &--warning {
    color: $color-character-counter-error;
    font-weight: bold;
  }

  &--over-limit {
    @include default-input-message-error;
    font-weight: bold;
  }

  &-container {
    .vds-character-counter-error {
      @include default-input-message-error;
      display: inline;

      [class^='vds-icon'] {
        @include svg-icon(1rem, theme(notification-error));
      }
    }
  }
}
