/***
  vds-list
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author dpoynor
*/

$color-list-bullet: theme(list-bullet-color); // $color-indigo

.vds-list {
  list-style: none;
  margin-top: 0;

  .vds-list-item {
    margin-bottom: px2em(12px);

    &::before {
      color: $color-list-bullet;
      content: '\25AA';
      display: inline-block;
      margin-left: px2em(26px) * -1;
      width: px2em(26px);
    }
  }

  &--ordered {
    counter-reset: li;

    .vds-list-item {
      &::before {
        content: '.'counter(li);
        counter-increment: li;
        direction: rtl;
        margin-left: px2em(42px) * -1;
        margin-right: px2em(15px);
        text-align: right;
      }
    }
  }
}
