.vds-cvv-information {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  margin: 0;
  min-width: 250px;
  padding-top: 8px;

  &__svg {
    background-repeat: no-repeat;
    background-size: contain;
    height: 44px;
    margin-right: 16px;
    position: relative;
    width: 70px;

    &--three-digit-img {
      background-image: url($three-digit-card-art);
    }

    &--four-digit-img {
      background-image: url($four-digit-card-art);
    }
  }
}
